.product-card{
    width: 100%;
    max-width: 300px;
    background-color: white;
    display: flex;
    flex-direction: column;
    cursor: pointer;
    margin: 0 auto;
    position: relative;
}

.card_image{
    width: 100%;
}

.card_infos{
padding: 20px;
border-top: 1px solid #ddd;
}

.card_price{
    font-size: 30px;
    font-weight:400;
    display: block;
    margin-bottom: 10px;
}

.card_title{
    font-size: 15px;
    color: rgba(0, 0 ,0, 0.5);
    line-height: 1.5;
    font-weight: 500;
}

.button_add-cart{
    position: absolute;
    top: 0;
    right: 0;
    width: 45px;
    height: 45px;
    margin: 12px 15px;
    color: #08241a;
    display: none;
    align-items: center;
    justify-content: center;
    border: none;
    border-radius: 50px;
    background-color: rgba(255, 255, 255, 0.8);
    font-size: 1.5rem;
    cursor: pointer;
}

.product-card:hover .button_add-cart{
    display: flex;
}